/*
React nav component
*/

import { slide as Menu } from 'react-burger-menu';

export default class TopNav extends React.Component {
  constructor(props) {
    super(props);

    this.style = {};

    this.state = {

    };
  }

  showSettings(event) {
    event.prefentDefault();
  }

  render() {
    var content = (
      <div>
      <Menu>
        <a id="home" className="menu-item" href='/'><i className='fa fa-fw fa-home'></i><span>Wicomico Trails 101</span></a>
        <a id="clubs" className="menu-item" href='/#clubs'><i className='fa fa-fw fa-users'></i><span>Walking Clubs</span></a>
        <a id="events" className="menu-item" href='/#events'><i className='fa fa-fw fa-calendar'></i><span>Events</span></a>
        <a id="map" className="menu-item" href='/#map'><i className='fa fa-fw fa-globe'></i><span>Trail Maps</span></a>
      </Menu>
</div>
      );

      return content;
    }
  };



